import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class PostCode extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        id="postCode"
        required
        name="postCode"
        onChange={this.handleChange}
        placeholder="00000"
        value={this.state.value}
        label="รหัสไปรษณีย์"
        fullWidth
        validators={['required', 'maxStringLength:9']}
        errorMessages={['จำเป็นต้องกรอก', 'ความยาวสูงสุด 9 ตัวอักษร']}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default PostCode;
