import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class TotalPackageAmount extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  render() {
    return (
      <TextValidator
        required
        name="totalPackageAmount"
        id="totalPackageAmount"
        onChange={this.handleChange}
        value={this.state.value}
        label="Total Package Amount"
        helperText="จำนวนหีบห่อรวม"
        placeholder="00000000"
        fullWidth
        validators={[
          'required',
          'minNumber:0',
          'maxNumber:99999999',
          'matchRegexp:^[0-9]*$',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'จำนวนต่ำสุดคือ 0',
          'กรอกได้ไม่เกิน 8 หลัก',
          'กรอกได้เฉพาะตัวเลข',
        ]}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default TotalPackageAmount;
