import React, { PureComponent, Fragment } from 'react';
import {
  fillHouseBillPlaceHolder,
  fillMasterBillPlaceHolder,
  fillVesselName,
  fillVesselNamePlaceHolder,
  setMasterBillAccess,
} from '../../../helperFunctions/billOfLadingFunctions';
import ModeOfTransport from './ModeOfTransport';
import VesselName from './VesselName';
import MasterBillOfLading from './MasterBillOfLading';
import HouseBillOfLading from './HouseBillOfLading';

class ModeOfTransportControl extends PureComponent {
  constructor() {
    super();
    this.state = {
      modeOfTransport: '',
      masterBillOfLading: '',
      masterBill: {
        placeholder: '',
        readOnly: true,
        required: true,
      },
      houseBillOfLading: '',
      houseBillPlaceHolder: '',
      vesselName: '',
      vessel: {
        placeholder: '',
        readOnly: true,
      },
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  handleModeOfTransportChange = e => {
    const value = e.target.value;
    this.setState({
      [e.target.name]: value,
      vessel: {
        placeholder: fillVesselNamePlaceHolder(value),
        readOnly: fillVesselName(value) === '' ? false : true,
      },
      vesselName: fillVesselName(value),
      masterBill: {
        placeholder: fillMasterBillPlaceHolder(value),
        readOnly: setMasterBillAccess(value),
        required: !setMasterBillAccess(value),
      },
      houseBillPlaceHolder: fillHouseBillPlaceHolder(value),
    });
  };

  render() {
    const {
      modeOfTransport,
      vesselName,
      vessel,
      masterBillOfLading,
      houseBillOfLading,
      masterBill,
      houseBillPlaceHolder,
    } = this.state;

    const { handleChange } = this;

    return (
      <Fragment>
        <ModeOfTransport
          value={modeOfTransport}
          handleChange={this.handleModeOfTransportChange}
        />
        <VesselName
          value={vesselName}
          placeholder={vessel.placeholder}
          handleChange={handleChange}
          readOnly={vessel.readOnly}
        />
        <MasterBillOfLading
          placeholder={masterBill.placeholder}
          handleChange={handleChange}
          value={masterBillOfLading}
          readOnly={masterBill.readOnly}
          isRequired={masterBill.required}
        />
        <HouseBillOfLading
          handleChange={handleChange}
          value={houseBillOfLading}
          placeholder={houseBillPlaceHolder}
        />
      </Fragment>
    );
  }
}

export default ModeOfTransportControl;
