import React from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { InputAdornment } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import TableInfoBody from './infoBody/tableInfoBody';
import masterBillData from './infoBody/masterBillData';

const MasterBillOfLading = React.memo(props => {
  const { handleChange, value, readOnly, isRequired, placeholder } = props;

  return (
    <TextValidator
      name="masterBillOfLading"
      id="masterBillOfLading"
      required={isRequired}
      onChange={handleChange}
      value={value}
      label="Master Bill Of Lading"
      placeholder={placeholder}
      InputProps={{
        readOnly: readOnly,
        endAdornment: (
          <InputAdornment position="end">
            <AdditionalInfoDialog
              titleTH="เลขที่ใบตราส่ง 1"
              titleEN="Master Bill Of Lading"
              format="an..35"
              maxWidth="sm"
            >
              <TableInfoBody
                firstHeader="หมวด"
                secondHeader="นำเข้าทาง"
                thirdHeader="กรอก"
                data={masterBillData}
              />
            </AdditionalInfoDialog>
          </InputAdornment>
        ),
      }}
      fullWidth
      validators={[
        'required',
        'matchRegexp:^[A-Za-z0-9]*$',
        'maxStringLength:35',
      ]}
      errorMessages={[
        'จำเป็นต้องกรอก',
        'กรอกเฉพาะตัวอักษรและตัวเลข',
        'ต้องกรอกไม่เกิน 35 หลัก',
      ]}
      variant="outlined"
      margin="dense"
    />
  );
});

export default MasterBillOfLading;
