import React, { PureComponent } from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';
import {
  InputAdornment,
  IconButton,
  Tooltip,
  Typography,
} from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import exchangeRateData from '../../../../jsonData/exchangeRateData.json';
import AutoRenewIcon from '@material-ui/icons/Autorenew';
import { handleChange } from '../../../shared/handleChange';

class ExchangeRate extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
      hasError: false,
      helperText: 'อัตราแลกเปลี่ยนเงินตรา',
    };
    this.handleChange = handleChange.bind(this);
  }

  getExchangeRate = () => {
    const currency = document.getElementById('currencyCode');
    const currencyObj = exchangeRateData.find(
      element => element.name === currency.value
    );
    currencyObj
      ? this.setState({
          value: parseFloat(currencyObj.value).toString(),
          hasError: false,
          helperText: 'อัตราแลกเปลี่ยนเงินตรา',
        })
      : this.setState({
          value: '',
          hasError: true,
          helperText: 'กรุณาเลือกสกุลเงินก่อน',
        });
  };

  render() {
    return (
      <TextValidator
        required
        error={this.state.hasError}
        name="exchangeRate"
        id="exchangeRate"
        onChange={this.handleChange}
        value={this.state.value}
        label="Exchange Rate"
        helperText={this.state.helperText}
        placeholder="0000.00000"
        fullWidth
        validators={[
          'required',
          'isFloat',
          'isCorrectExchangeRate',
          'minFloat:0.000001',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'ต้องเป็นตัวเลขและจุดทศนิยมเท่านั้น',
          'กรุณากรอกตามรูปแบบ (คลิกไอคอน i เพื่อดูเพิ่มเติม)',
          'ต้องมีค่ามากกว่า 0',
        ]}
        margin="dense"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <Tooltip title="ดึงค่าอัตราแลกเปลี่ยน">
                <IconButton onClick={this.getExchangeRate}>
                  <AutoRenewIcon />
                </IconButton>
              </Tooltip>
              <AdditionalInfoDialog
                titleTH="อัตราแลกเปลี่ยนเงินตรา"
                titleEN="Exchange Rate"
                format="n..12,5"
                maxWidth="xs"
              >
                <Typography gutterBottom>
                  ข้อมูลอัตราแลกเปลี่ยนเดือนมกราคม 2019
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      />
    );
  }
}

export default ExchangeRate;
