import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class CompanyBranch extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        name="companyBranch"
        id="companyBranch"
        required
        type="number"
        onChange={this.handleChange}
        value={this.state.value}
        label="สาขาที่อยู่ของบริษัทผู้นำเข้า"
        placeholder="000000"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[0-9]*$',
          'maxStringLength:6',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวเลข 0-9',
          'ต้องกรอกไม่เกิน 6 หลัก',
        ]}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default CompanyBranch;
