import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class CustomsClearanceName extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        name="customsClearanceName"
        id="customsClearanceName"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="Customs Clearance Name"
        helperText="ชื่อและนามสกุลผู้ปฏิบัติหน้าที่เกี่ยวกับการผ่านพิธีการศุลกากร"
        fullWidth
        validators={['required', 'maxStringLength:35']}
        errorMessages={['จำเป็นต้องกรอก', 'ต้องกรอกไม่เกิน 35 หลัก']}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default CustomsClearanceName;
