import React, { PureComponent } from 'react';
import { SelectValidator } from 'react-material-ui-form-validator';
import { MenuItem } from '@material-ui/core';
import weightUnitCodes from '../selectionData/weightUnitCodes';
import { handleChange } from '../../../../shared/handleChange';

class WeightUnitCodeSelect extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    const { id, label, helperText } = this.props;
    return (
      <SelectValidator
        id={id}
        required
        name={id}
        onChange={this.handleChange}
        value={this.state.value}
        select
        label={label}
        helperText={helperText}
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องเลือก']}
        margin="dense"
        variant="outlined"
      >
        {weightUnitCodes.map(option => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </SelectValidator>
    );
  }
}

export default WeightUnitCodeSelect;
