import React, { PureComponent, Fragment } from 'react';
import BrokerTaxNumber from './BrokerTaxNumber';
import BrokerBranch from './BrokerBranch';

class BrokerFieldWrapper extends PureComponent {
  state = {
    brokerTaxNumber: '',
    brokerBranch: '',
  };

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  render() {
    const { handleChange } = this;
    return (
      <Fragment>
        <BrokerTaxNumber
          value={this.state.brokerTaxNumber}
          handleChange={handleChange}
        />
        {this.state.brokerTaxNumber.length > 0 && (
          <BrokerBranch
            value={this.state.brokerBranch}
            handleChange={handleChange}
          />
        )}
      </Fragment>
    );
  }
}

export default BrokerFieldWrapper;
