import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class CompanyNameTH extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        name="companyNameTH"
        id="companyNameTH"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="ชื่อบริษัทภาษาไทย"
        placeholder="บริษัท เฮฟวี่สโตนเอ็กซ์เพรส จำกัด"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[^A-Za-z]*$',
          'maxStringLength:120',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรุณากรอกเป็นภาษาไทยล้วน',
          'ความยาวสูงสุด 120 ตัวอักษร',
        ]}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default CompanyNameTH;
