import React, { PureComponent } from 'react';
import SelectValidator from 'react-material-ui-form-validator/lib/SelectValidator';
import { MenuItem } from '@material-ui/core';
import cargoTypeCodeData from './data/cargoTypeCodeData';
import { handleChange } from '../../../shared/handleChange';

class CargoTypeCode extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <SelectValidator
        name="cargoTypeCode"
        id="cargoTypeCode"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="Cargo Type Code"
        helperText="ประเภทของการบรรจุตามมาตรฐาน UNECE Recommendation 21"
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องกรอก']}
        variant="outlined"
        margin="dense"
      >
        {cargoTypeCodeData.map(option => (
          <MenuItem key={option.code} value={option.code}>
            {option.name}
          </MenuItem>
        ))}
      </SelectValidator>
    );
  }
}

export default CargoTypeCode;
