import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class CompanyNameEN extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        name="companyNameEN"
        id="companyNameEN"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="ชื่อบริษัทภาษาอังกฤษ"
        placeholder="Heavy Stone Express Co., Ltd."
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[^ขฃฉฐถผฝศษสหกจฎฏดตบปอคฅฆงชซฌญฑฒณทธนพฟภมยรลวฬฮ]*$',
          'maxStringLength:70',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรุณากรอกเป็นภาษาอังกฤษล้วน',
          'ความยาวสูงสุด 70 ตัวอักษร',
        ]}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default CompanyNameEN;
