import React, { Fragment, PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { InputAdornment, Typography } from '@material-ui/core';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import { handleChange } from '../../../shared/handleChange';

class ReferenceNumber extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  render() {
    return (
      <TextValidator
        name="referenceNumber"
        id="referenceNumber"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="เลขที่อ้างอิง"
        margin="dense"
        placeholder="xxxxnnnnnnnnnxxxx"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <AdditionalInfoDialog
                maxWidth="xs"
                titleTH="เลขที่อ้างอิง"
                titleEN="Reference Number"
                format="an13"
              >
                <Fragment>
                  <Typography color="textSecondary">
                    เลขที่อ้างอิง xxxxnnnnnnnnnxxxx
                  </Typography>
                  <Typography color="textSecondary">
                    (4) = Profile Name nnnnnnnnn
                  </Typography>
                  <Typography color="textSecondary">
                    (9) = Running Number
                  </Typography>
                </Fragment>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[A-Za-z0-9]*$',
          'minStringLength:13',
          'maxStringLength:13',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวอักษร A-Z และเลข 0-9',
          'ต้องกรอกให้ครบ 13 หลัก',
          'ต้องกรอกไม่เกิน 13 หลัก',
        ]}
      />
    );
  }
}

export default ReferenceNumber;
