import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class ApprovalNumber extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  render() {
    return (
      <TextValidator
        name="approvalNumber"
        id="approvalNumber"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="Approval Number"
        helperText="เลขที่อนุมัติหลายเที่ยวเรือ"
        placeholder="YY0000"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[0-9]*$',
          'maxStringLength:6',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวเลข',
          'ต้องกรอกไม่เกิน 6 หลัก',
        ]}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default ApprovalNumber;
