import React, { PureComponent, Fragment } from 'react';
import TotalNetWeight from './TotalNetWeight';
import NetWeightUnitCode from './NetWeightUnitCode';
import TotalGrossWeight from './TotalGrossWeight';
import TotalGrossWeightUnitCode from './TotalGrossWeightUnitCode';
import { addCustomValidationRules } from '../../../shared/customValidations';

class WeightControl extends PureComponent {
  constructor() {
    super();
    this.state = {
      totalNetWeight: '',
      totalGrossWeight: '',
    };
  }

  handleChange = e => {
    this.setState({
      [e.target.name]: e.target.value,
    });
  };

  componentWillMount() {
    addCustomValidationRules(this);
  }

  render() {
    return (
      <Fragment>
        <TotalNetWeight
          totalNetWeight={this.state.totalNetWeight}
          handleChange={this.handleChange}
        />
        <NetWeightUnitCode />
        <TotalGrossWeight
          totalGrossWeight={this.state.totalGrossWeight}
          handleChange={this.handleChange}
        />
        <TotalGrossWeightUnitCode />
      </Fragment>
    );
  }
}
export default WeightControl;
