import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class ManagerID extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        name="managerId"
        id="managerId"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="Manager ID Card"
        helperText="เลขบัตรประชาชนผู้จัดการ / ผู้รับมอบอำนาจของบริษัทหรือ Broker"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[A-Za-z0-9]*$',
          'maxStringLength:17',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกเฉพาะตัวอักษรหรือตัวเลข',
          'ต้องกรอกไม่เกิน 17 หลัก',
        ]}
        variant="outlined"
        margin="dense"
      />
    );
  }
}

export default ManagerID;
