import React, { PureComponent } from 'react';
import TextValidator from 'react-material-ui-form-validator/lib/TextValidator';
import { handleChange } from '../../../shared/handleChange';

class RegistrationID extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <TextValidator
        id="registrationID"
        name="registrationID"
        onChange={this.handleChange}
        value={this.state.value}
        fullWidth
        label="Registration ID"
        helperText="รหัสผู้ส่งข้อมูล"
        validators={['required', 'maxStringLength: 35']}
        errorMessages={['จำเป็นต้องกรอก', 'กรอกได้สูงสุด 35 ตัวอักษร']}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default RegistrationID;
