import React, { Fragment, PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class StreetAndNumber extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  render() {
    return (
      <Fragment>
        <TextValidator
          name="streetAndNumber"
          id="streetAndNumber"
          required
          onChange={this.handleChange}
          value={this.state.value}
          label="ที่อยู่บริษัท"
          placeholder="เลขที่ 5 ถนนเจริญกรุง"
          fullWidth
          validators={['required', 'maxStringLength:70']}
          errorMessages={['จำเป็นต้องกรอก', 'ต้องกรอกไม่เกิน 70 ตัวอักษร']}
          variant="outlined"
          margin="dense"
        />
      </Fragment>
    );
  }
}

export default StreetAndNumber;
