import React, { PureComponent } from 'react';
import { TextValidator } from 'react-material-ui-form-validator';
import { handleChange } from '../../../shared/handleChange';

class ApprovalPort extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }

  render() {
    return (
      <TextValidator
        name="approvalPort"
        id="approvalPort"
        required
        onChange={this.handleChange}
        value={this.state.value}
        label="Approval Port"
        helperText="รหัสสถานที่อนุมัติใบขนสินค้าหลายเที่ยวเรือ"
        placeholder="0000"
        fullWidth
        validators={[
          'required',
          'matchRegexp:^[0-9]*$',
          'maxStringLength:4',
        ]}
        errorMessages={[
          'จำเป็นต้องกรอก',
          'กรอกได้เฉพาะตัวเลข',
          'ต้องกรอกไม่เกิน 4 หลัก',
        ]}
        margin="dense"
        variant="outlined"
      />
    );
  }
}

export default ApprovalPort;
