import React, { PureComponent } from 'react';
import { Typography, InputAdornment, MenuItem } from '@material-ui/core';
import { SelectValidator } from 'react-material-ui-form-validator';
import AdditionalInfoDialog from '../../../shared/AdditionalInfoDialog';
import { handleChange } from '../../../shared/handleChange';

class AssessmentRequestCode extends PureComponent {
  constructor() {
    super();
    this.state = {
      value: '',
    };
    this.handleChange = handleChange.bind(this);
  }
  render() {
    return (
      <SelectValidator
        id="assessmentRequestCode"
        required
        name="assessmentRequestCode"
        onChange={this.handleChange}
        value={this.state.value}
        select
        label="Assessment Request Code"
        helperText="ต้องการขอพบเจ้าหน้าที่"
        fullWidth
        validators={['required']}
        errorMessages={['จำเป็นต้องเลือก']}
        margin="dense"
        variant="outlined"
        InputProps={{
          endAdornment: (
            <InputAdornment position="end">
              <AdditionalInfoDialog
                titleTH="ขอพบเจ้าหน้าที่"
                titleEN="Assessment Request Code"
                format="a1"
                maxWidth="xs"
              >
                <Typography>
                  ต้องการขอพบเจ้าหน้าที่ เนื่องจากมีปัญหาในเรื่องพิกัด ราคา
                  หรือต้องการให้ตรวจสอบของ
                </Typography>
              </AdditionalInfoDialog>
            </InputAdornment>
          ),
        }}
      >
        <MenuItem value="Y">ต้องการขอพบเจ้าหน้าที่</MenuItem>
        <MenuItem value="N">ไม่ต้องการขอพบเจ้าหน้าที่</MenuItem>
      </SelectValidator>
    );
  }
}

export default AssessmentRequestCode;
