import React, { PureComponent } from 'react';
import { ValidatorForm } from 'react-material-ui-form-validator';
import ReferenceNumber from './fields/idc1Fields/ReferenceNumber';
import AddressSelectors from './fields/idc1Fields/AddressSelectors';
import PostCode from './fields/idc1Fields/PostCode';
import FieldHeader from '../shared/FieldHeader';
import DocumentType from './fields/idc1Fields/DocumentType';
import CompanyTaxNumber from './fields/idc1Fields/CompanyTaxNumber';
import CompanyBranch from './fields/idc1Fields/CompanyBranch';
import CompanyNameTH from './fields/idc1Fields/CompanyNameTH';
import CompanyNameEN from './fields/idc1Fields/CompanyNameEN';
import StreetAndNumber from './fields/idc1Fields/StreetAndNumber';
import SubmitButton from '../shared/SubmitButton';

const fieldList = [
  'referenceNumber',
  'documentType',
  'companyTaxNumber',
  'companyBranch',
  'companyNameTH',
  'companyNameEN',
  'streetAndNumber',
  'companyTambon',
  'subProvince',
  'province',
  'postCode',
];

class ImportDeclarationControl1 extends PureComponent {
  componentDidMount() {
    fieldList.forEach(field => {
      if (localStorage.getItem(field)) {
        this.setState({
          [field]: localStorage.getItem(field),
        });
      }
    });
  }

  saveToLocalStorage = data => {
    for (const pair of data.entries()) {
      localStorage.setItem(pair[0], pair[1]);
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    this.saveToLocalStorage(data);
    this.props.handlenext();
  };

  render() {
    const { handleSubmit } = this;
    return (
      <ValidatorForm
        ref="form"
        onSubmit={handleSubmit}
        onError={errors => console.log(errors)}
        noValidate
        autoComplete="off"
      >
        <ReferenceNumber />
        <DocumentType />
        <FieldHeader>ข้อมูลบริษัทนำเข้า</FieldHeader>
        <CompanyTaxNumber />
        <CompanyBranch />
        <CompanyNameTH />
        <CompanyNameEN />
        <FieldHeader>ที่อยู่บริษัทนำเข้า</FieldHeader>
        <StreetAndNumber />
        <AddressSelectors />
        <PostCode />
        <SubmitButton />
      </ValidatorForm>
    );
  }
}

export default ImportDeclarationControl1;
